import { useState, useEffect, useRef } from 'react';
import { Helmet } from 'react-helmet';
import Intro from 'pages/Home/Intro';
import ProjectSummary from 'pages/Home/ProjectSummary';
import Profile from 'pages/Home/Profile';
import Image from 'components/Image';
import Footer from 'components/Footer';
import { usePrefersReducedMotion, useRouteTransition } from 'hooks';
import { useLocation } from 'react-router-dom';
import sprTexturePlaceholder from 'assets/recorderr-placeholder.png';
import sprTexture from 'assets/recorderr-normal.png';
import sprTextureLarge from 'assets/recorderr-large.png';
import gamestackTexturePlaceholder from 'mine/Blur-Present.png';
import gamestackTexture from 'mine/Normal-Present.png';
import gamestackTextureLarge from 'mine/Large-Present.png';
import gamestackTexture2Placeholder from 'mine/Blur-Present2.png';
import gamestackTexture2 from 'mine/Normal-Present2.png';
import gamestackTexture2Large from 'mine/Large-Present2.png';
import wheelTexturePlaceholder from 'mine/Blur-Wheel.png';
import wheelTexture from 'mine/Normal-Wheel.png';
import wheelTextureLarge from 'mine/Large-Wheel.png';
import wheelTexture2Placeholder from 'mine/Blur-Wheel2.png';
import wheelTexture2 from 'mine/Normal-Wheel2.png';
import wheelTexture2Large from 'mine/Large-Wheel2.png';
import sliceTexture from 'mine/image.jpg.png';
import sliceTextureLarge from 'mine/image-large.png';
import sliceTexturePlaceholder from 'mine/image-blur.png';
import galaxyStore from 'mine/galaxyStore.png';
import iphone11 from 'assets/iphone-11.glb';
import macbookPro from 'assets/macbook-pro.glb';
import './index.css';

const disciplines = ['Designer', 'Gamer', 'Animator', 'Entrepreneur'];

const Home = () => {
  const { status } = useRouteTransition();
  const { hash, state } = useLocation();
  const initHash = useRef(true);
  const [visibleSections, setVisibleSections] = useState([]);
  const [scrollIndicatorHidden, setScrollIndicatorHidden] = useState(false);
  const intro = useRef();
  const projectOne = useRef();
  const projectTwo = useRef();
  const projectThree = useRef();
  const project4 = useRef();
  const details = useRef();
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    const revealSections = [intro, projectOne, projectTwo, projectThree, project4, details];

    const sectionObserver = new IntersectionObserver(
      (entries, observer) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const section = entry.target;
            observer.unobserve(section);
            if (visibleSections.includes(section)) return;
            setVisibleSections(prevSections => [...prevSections, section]);
          }
        });
      },
      { rootMargin: '0px 0px -10% 0px' }
    );

    const indicatorObserver = new IntersectionObserver(
      ([entry]) => {
        setScrollIndicatorHidden(!entry.isIntersecting);
      },
      { rootMargin: '-100% 0px 0px 0px' }
    );

    revealSections.forEach(section => {
      sectionObserver.observe(section.current);
    });

    indicatorObserver.observe(intro.current);

    return () => {
      sectionObserver.disconnect();
      indicatorObserver.disconnect();
    };
  }, [visibleSections]);

  useEffect(() => {
    const hasEntered = status === 'entered';
    const supportsNativeSmoothScroll = 'scrollBehavior' in document.documentElement.style;
    let scrollObserver;
    let scrollTimeout;

    const handleHashchange = (hash, scroll) => {
      clearTimeout(scrollTimeout);
      const hashSections = [intro, projectOne, details];
      const hashString = hash.replace('#', '');
      const element = hashSections.filter(item => item.current.id === hashString)[0];
      if (!element) return;
      const behavior = scroll && !prefersReducedMotion ? 'smooth' : 'instant';
      const top = element.current.offsetTop;

      scrollObserver = new IntersectionObserver(
        (entries, observer) => {
          const [entry] = entries;
          if (entry.isIntersecting) {
            scrollTimeout = setTimeout(
              () => {
                element.current.focus();
              },
              prefersReducedMotion ? 0 : 400
            );
            observer.unobserve(entry.target);
          }
        },
        { rootMargin: '-20% 0px -20% 0px' }
      );

      scrollObserver.observe(element.current);

      if (supportsNativeSmoothScroll) {
        window.scroll({
          top,
          left: 0,
          behavior,
        });
      } else {
        window.scrollTo(0, top);
      }
    };

    if (hash && initHash.current && hasEntered) {
      handleHashchange(hash, false);
      initHash.current = false;
    } else if (!hash && initHash.current && hasEntered) {
      window.scrollTo(0, 0);
      initHash.current = false;
    } else if (hasEntered) {
      handleHashchange(hash, true);
    }

    return () => {
      clearTimeout(scrollTimeout);
      if (scrollObserver) {
        scrollObserver.disconnect();
      }
    };
  }, [hash, state, prefersReducedMotion, status]);

  return (
    <div className="home">
      <Helmet>
        <title>Watan Asaad</title>
        <meta
          name="description"
          content="Portfolio of Watan Asaad"
        />
        <link rel="prefetch" href={iphone11} as="fetch" crossorigin="" />
        <link rel="prefetch" href={macbookPro} as="fetch" crossorigin="" />
      </Helmet>
      <Intro
        id="intro"
        sectionRef={intro}
        disciplines={disciplines}
        scrollIndicatorHidden={scrollIndicatorHidden}
      />
      <ProjectSummary
        id="project-1"
        sectionRef={projectOne}
        visible={visibleSections.includes(projectOne.current)}
        index={1}
        title="Record and brodcast your screen, Anytime, Anywhere!"
        description="Use Recorderr to record your screen, or your gameplay with one click for free!, with the highest quality possible and using the easiest methods."
        buttonText="Download"
        buttonLink="https://drive.google.com/drive/folders/1a7DIItX786KMFo5nfwzyyY7dc_aad18F?usp=sharing"
        model={{
          type: 'laptop',
          alt: 'Smart Sparrow lesson builder',
          textures: [
            {
              src: sprTexture,
              srcSet: `${sprTexture} 800w, ${sprTextureLarge} 1440w`,
              placeholder: sprTexturePlaceholder,
            },
          ],
        }}
      />
      <ProjectSummary
        id="project-2"
        alternate
        sectionRef={project4}
        visible={visibleSections.includes(project4.current)}
        index={2}
        title="Super Wheel, Roll Your Way Into Life."
        description="A simple 3D Endless runner game, Collect gems to upgrade the look of your wheel, the further you make it the harder it gets!."
        buttonText="Download"
        buttonLink="https://apps.samsung.com/appquery/appDetail.as?appId=com.WatanAsaad.SuperWheel"
        model={{
          type: 'phone',
          alt: 'App login screen',
          textures: [
            {
              src: wheelTexture,
              srcSet: `${wheelTexture} 254w, ${wheelTextureLarge} 508w`,
              placeholder: wheelTexturePlaceholder,
            },
            {
              src: wheelTexture2,
              srcSet: `${wheelTexture2} 254w, ${wheelTexture2Large} 508w`,
              placeholder: wheelTexture2Placeholder,
            },
          ],
        }}
      />
      <Image  srcSet={galaxyStore} className="galaxy"/>
      <ProjectSummary
        id="project-3"
        sectionRef={projectThree}
        visible={visibleSections.includes(projectThree.current)}
        index={3}
        title="Developing The Next Gen Realastic game, With Unbelievable Graphics."
        description="My First project with Desktop Games, And will be one of the biggest, With Good game mechanics and story. The game launch is planned to be on 2022/2023."
        buttonText="See Detalis"
        buttonLink="/projects/game"
        model={{
          type: 'laptop',
          alt: 'Annotating a biomedical image in the Slice app',
          textures: [
            {
              src: sliceTexture,
              srcSet: `${sliceTexture} 980w, ${sliceTextureLarge} 1376w`,
              placeholder: sliceTexturePlaceholder,
            },
          ],
        }}
      />
      <ProjectSummary
        id="project-4"
        alternate
        sectionRef={projectTwo}
        visible={visibleSections.includes(projectTwo.current)}
        index={4}
        title="Building A Beatiful Life-Changer App To organize your daily life"
        description="Listivo, The App that is going to help you track your progress, Organize your daily tasks, And be more productive. "
        buttonText="View Project"
        buttonLink="/projects/listivo"
        model={{
          type: 'phone',
          alt: 'App login screen',
          textures: [
            {
              src: gamestackTexture,
              srcSet: `${gamestackTexture} 254w, ${gamestackTextureLarge} 508w`,
              placeholder: gamestackTexturePlaceholder,
            },
            {
              src: gamestackTexture2,
              srcSet: `${gamestackTexture2} 254w, ${gamestackTexture2Large} 508w`,
              placeholder: gamestackTexture2Placeholder,
            },
          ],
        }}
      />
      <Profile
        sectionRef={details}
        visible={visibleSections.includes(details.current)}
        id="details"
      />
      <Footer />
    </div>
  );
};

export default Home;
